#container {
  text-align: center;
  height: 350px;
  width: 700px;
  background: rgba(255, 255, 255, .9);
  border-radius: 5px;
  margin: 5em auto;
  overflow: hidden;
  box-shadow: 0 15px 30px 1px rgba(128, 128, 128, .31);
}

.product-details {
  text-align: left;
  height: 100%;
  float: left;
  width: 40%;
  padding: 30px;
  position: relative;
  overflow: hidden;
}

#container .product-details h1 {
  color: #344055;
  margin: 0;
  font-family: Old Standard TT, serif;
  font-size: 34px;
  display: inline-block;
  position: relative;
}

#container .product-details h1:before {
  content: "";
  color: #fff;
  background: #ffa69e;
  border-radius: 5px;
  margin: 0;
  padding: 5px;
  font-family: Farsan, cursive;
  font-size: 14px;
  animation: chan-sh 6s infinite;
  display: inline-block;
  position: absolute;
  top: 0%;
  right: 0%;
  transform: translate(25px, -15px);
}

@keyframes chan-sh {
  from {
    content: "free shipping";
  }

  to {
    content: "New";
  }
}

.hint-star {
  color: gold;
  width: 50%;
  margin-left: .5em;
  display: inline-block;
}

#container .product-details > p {
  text-align: center;
  color: #7d7d7d;
  font-family: Farsan, cursive;
  font-size: 20px;
}

.control {
  position: absolute;
  bottom: 20%;
  left: 22.8%;
}

.btn {
  cursor: pointer;
  color: #eee;
  background: #49c608;
  border: none;
  border-radius: 5px;
  outline: none;
  margin: 0;
  padding: 0;
  transition: all .3s linear;
  position: relative;
  overflow: hidden;
  transform: translateY(0);
}

.btn:hover {
  transform: translateY(-4px);
}

.btn span {
  margin: 0;
  padding: 10px 20px;
  font-family: Farsan, cursive;
  font-size: 1.2em;
  transition: transform .3s;
  display: inline-block;
}

.btn .price, .shopping-cart {
  background: #333;
  border: 0;
  margin: 0;
}

.btn .price {
  padding-right: 15px;
  transform: translateX(-10%);
}

.btn .shopping-cart {
  z-index: 1;
  background: #333;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(-100%);
}

.btn .buy {
  z-index: 3;
  font-weight: bolder;
}

.btn:hover .price {
  transform: translateX(-110%);
}

.btn:hover .shopping-cart {
  transform: translateX(0%);
}

.product-image {
  height: 100%;
  float: right;
  width: 50%;
  transition: all .3s ease-out;
  display: inline-block;
  position: relative;
  overflow: hidden;
}

#container img {
  width: 100%;
  height: 100%;
}

.info {
  text-align: left;
  cursor: no-drop;
  color: #fff;
  height: 100%;
  width: 100%;
  background: rgba(27, 26, 26, .9);
  font-family: Farsan, cursive;
  font-size: 120%;
  line-height: 1.9;
  transition: all .3s ease-out;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(-100%);
}

.info h2 {
  text-align: center;
}

.product-image:hover .info {
  transform: translateX(0);
}

.info ul li {
  transition: all .3s;
}

.info ul li:hover {
  transform: translateX(50px)scale(1.3);
}

.product-image:hover img {
  transition: all .3s ease-out;
  transform: scale(1.2);
}

.loader {
  width: 100%;
  max-width: 6rem;
  justify-content: center;
  align-items: center;
  margin-top: 3rem;
  margin-bottom: 3rem;
  display: flex;
  position: relative;
}

.loader:before, .loader:after {
  content: "";
  filter: drop-shadow(0 0 1rem rgba(255, 255, 255, .75));
  border-radius: 50%;
  animation: pulsOut 1.8s ease-in-out infinite;
  position: absolute;
}

.loader:before {
  width: 100%;
  padding-bottom: 100%;
  animation-name: pulsIn;
  box-shadow: inset 0 0 0 1rem #fff;
}

.loader:after {
  width: calc(100% - 2rem);
  padding-bottom: calc(100% - 2rem);
  box-shadow: 0 0 #fff;
}

@keyframes pulsIn {
  0% {
    opacity: 1;
    box-shadow: inset 0 0 0 1rem #fff;
  }

  50%, 100% {
    opacity: 0;
    box-shadow: inset 0 0 #fff;
  }
}

@keyframes pulsOut {
  0%, 50% {
    opacity: 0;
    box-shadow: 0 0 #fff;
  }

  100% {
    opacity: 1;
    box-shadow: 0 0 0 1rem #fff;
  }
}

